class Obstacle {
  constructor(blockX, blockWidth, blockY, blockHeight, blockVel) {
    this.blockX = blockX;
    this.blockWidth = blockWidth;
    this.blockY = blockY;
    this.blockHeight = blockHeight;
    this.blockVel = blockVel;
  }
}

export default Obstacle;



